body {
  color: #999;
  background: #222;
}

body img {
  width: 50px;
  top: 20px;
  left: 20px;
}

body form {
  border: 2px solid #999;
  border-radius: 20px;
  padding: 20px;
}

body form .input-container input {
  color: #999;
  background: none;
  border: 2px solid #999;
  border-radius: 20px;
  margin-bottom: 20px;
  padding: 10px 35px 10px 20px;
}

body form .input-container input:focus {
  outline: none;
}

body form .input-container i {
  cursor: pointer;
  position: absolute;
  top: 32px;
  right: 30px;
}

body form .contains ul li.active {
  color: #00ff004d;
  text-decoration: line-through;
  transition: all .3s;
}

@media (width <= 450px) {
  body form {
    margin: 10px;
    padding: 10px;
  }
}

